import styled from "styled-components";

const Button = styled.button`
  -webkit-appearance: none;
  border: 1px solid #ccc;
  padding: 8px 14px;
  outline: none;
  background-color: #fff;
  cursor: pointer;
  font-weight: 600;
  border-radius: 3px;
`;

export default Button;


